@media print, screen and (min-width: 769px) {
  :focus:not(:focus-visible),
  :where(:root[data-mousedown] dialog *),
  [data-modal-open]:where(:root[data-mousedown] *) {
    outline: none;
  }
  .modal_dialog {
    padding: 0;
    max-width: 600px;
    width: 100%;
    max-height: calc(100vh - 60px);
    opacity: 0;
    transition: opacity 0.3s ease;
  }
  _::-webkit-full-page-media, _:future, :root .modal_dialog {
    max-height: calc(100svh - 60px);
  }
  .modal_dialog[data-modal-active=true] {
    opacity: 1;
  }
  .modal_dialog::backdrop {
    backdrop-filter: blur(4px);
    background-color: rgba(0, 0, 0, 0.7);
    cursor: pointer;
  }
  .modal_dialog .dialog_content {
    padding: 50px 30px;
    background-color: #ccc;
  }
  .modal_dialog .dialog_close {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    overflow: hidden;
    background-color: transparent;
    width: 50px;
    padding: 25px 0;
    cursor: pointer;
  }
  .modal_dialog .dialog_close::before, .modal_dialog .dialog_close::after {
    content: "";
    display: block;
    position: absolute;
    right: 0;
    left: 0;
    margin-right: auto;
    margin-left: auto;
    background-color: #716e77;
    width: 60%;
    height: 2px;
  }
  .modal_dialog .dialog_close::before {
    top: calc(50% - 2px / 2);
    transform: rotate(45deg);
  }
  .modal_dialog .dialog_close::after {
    bottom: calc(50% - 2px / 2);
    transform: rotate(-45deg);
  }
}
